export const getNow = (isHour12 = false) => {

  let d = new Date();
  let now = d.toLocaleString("es-ES", {
    hour: "2-digit",
    minute: "2-digit",
    hour12: isHour12,
  });
  return now;
};

export const processContext = (msg) => {
  const data = JSON.parse(msg);
  let textContext='';
  data.forEach((item) => {
    if (item.type==='text'){
      textContext = textContext + '<hr><br>' + (item.fromUser ? '<strong>Ciudadano:</strong><br>':'<strong>Ayuntamiento (bot):</strong><br>') + item.payload.text;
    }
  })
  return textContext;
}

export const checkItemStorage = () => {
  if (window.localStorage.getItem("oda-widget-status-date")) {
    const now = new Date().getTime();
    console.log("now", now);
    let diff =
      (now - window.localStorage.getItem("oda-widget-status-date")) / 1000;
    diff /= 60;
    console.log("diff", diff);

    if (diff > 5) {
      window.localStorage.removeItem("oda-widget-status-date");
    }
  }
};
